$bgWhite:                  linear-gradient(90deg, #FFFFFF 100%, #FFFFFF 100%)
$selectBg:                 rgba(255, 255, 255, .66)
/*
//General settings =====================================================================================================
/*
//Emergency fonts
$font-family-sans-serif: system-ui,-apple-system,"Segoe UI",Roboto,"Helvetica Neue", Arial,"Noto Sans","Liberation Sans",sans-serif,"Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !default
//Main font
$mainFontFamily:          'Manrope'
//Font size
//$fontSize:              ('size': 16)
$fontSizeVar:             16
//Main color
$basicColor:              ('color': #000000)
//Main background
$basicBackground:         ('color': inherit)
//Min width
$minWidth:                 320px
//Max width viewport
$mW:                      ('size': 1920)
//Max width container
$mWContainer:             ('size': 1454)
//Container padding left and right
$paddingContainer:        ('size': 30)
/*
//Header settings ======================================================================================================
/*
//Header background
$bgHeader:                ('color': transparent)
/*
//Links style ==========================================================================================================
/*

//Color active link
$activeLinkColor:         ('color': $neutral-white)
/*
//Menu burger style ====================================================================================================
/*
//Background icon burger
$backgroundIconBurger:               ('color': transparent)
//Background span icon burger
$backgroundSpanIconBurger:           ('color': #FFFFFF)
//Background span active icon burger
$backgroundSpanActiveIconBurger:     ('color': #FFFFFF)
//Background menu burger
$backgroundMenuBurger:               ('color': rgba(63, 188, 98, 1))
/*
//Popup style settings =================================================================================================
/*
//Color cross popup
$colorCrossPopup:           ('color': #FFFFFF)
//Background color popup
$backgroundColorPopup:      ('color': rgba(0, 0, 0, .55))
//Width body popup
$maxWidthBodyPopup:         ('size': 800px)
//Main padding popup
$m-pTop:                     'padding-top',    30, 15, 1
$m-pLeft:                    'padding-bottom', 30, 15, 1
$m-pRight:                   'padding-left',   30, 15, 1
$m-pBottom:                  'padding-right',  30, 15, 1
//Body padding popup
$b-pTop:                     'padding-top',    30, 15, 1
$b-pLeft:                    'padding-bottom', 30, 15, 1
$b-pRight:                   'padding-left',   30, 15, 1
$b-pBottom:                  'padding-right',  30, 15, 1



//$md1:       $mWContainer + 20
$md2:                      992
$md3:                      768
$md4:                      480
